body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


body{
background-color: #f5f6f8


}

.main {
  padding: 0 50px;
  background-color:hsla(0, 0%, 100%, 0.7);
  width:400px;
  height:300px;
  position:absolute;
  left:50%;
  margin-left: -200px;
  top:50%;
  margin-top:-150px;
  border-radius: 6px;
  border:1px solid #dfdfdf
}

h3{
 text-align: center;
 margin-top: 10px !important;
 color: #cccccc !important;
}

.item{
   padding: 10px;

}
.logo {
  width: 120px;
  height: 31px;
  background: rgba(255, 255, 255, .2);
  margin: 16px 24px 16px 0;
  float: left;
}

.my-header {
  padding: 0px 20px 0px 100px;
}

.ant-menu-item:hover,
.ant-menu-item-active,
.ant-menu:not(.ant-menu-inline) .ant-menu-submenu-open,
.ant-menu-submenu-active,
.ant-menu-submenu-title:hover {
  color: #fff;
}

.ant-menu-dark .ant-menu-item:hover,
.ant-menu-dark .ant-menu-item-active,
.ant-menu-dark .ant-menu-submenu-active,
.ant-menu-dark .ant-menu-submenu-open,
.ant-menu-dark .ant-menu-submenu-selected,
.ant-menu-dark .ant-menu-submenu-title:hover {
  background-color: #2593fc !important;
  color: #fff
}

.touxiang {
  width: 40px;
  height: 40px;
  margin: 12px 24px 12px 0;
  float: left;
  border-radius: 50%;
}

.userName {
  position: absolute;
  left: 20px;
  color: #fff;
  top: 0px;
  width: 80px;
  text-align: center;
}
.Index-wrapper {
  width: 100%;
  min-height: 100vh;
  padding: 30px; }
  .Index-wrapper .site-card-wrapper {
    width: 100%; }
  .Index-wrapper .site-card-rate {
    width: 100%;
    margin-top: 10px; }
  .Index-wrapper .site-card-still {
    width: 100%;
    margin-top: 10px; }
  .Index-wrapper .chart1 {
    width: 100%;
    margin-top: 10px; }
    .Index-wrapper .chart1 .title {
      font-size: 24px;
      font-weight: bold; }
    .Index-wrapper .chart1 img {
      width: 100%; }
  .Index-wrapper .chart2 {
    width: 100%;
    margin-top: 10px;
    padding: 20px;
    background: #fff; }
    .Index-wrapper .chart2 .title {
      font-size: 24px;
      font-weight: bold; }
    .Index-wrapper .chart2 img {
      width: 100%; }
    .Index-wrapper .chart2 .my-search {
      width: 300px;
      margin-top: 10px; }
    .Index-wrapper .chart2 .my-tabs-name {
      display: inline-block;
      margin-left: 50px;
      margin-right: 10px; }
    .Index-wrapper .chart2 .my-textarea {
      width: 800px;
      margin-right: 20px; }
    .Index-wrapper .chart2 .my-btn {
      float: right;
      margin-bottom: 10px; }
    .Index-wrapper .chart2 .my-icon {
      width: 40px;
      height: 40px; }
    .Index-wrapper .chart2 .team-table {
      background: #fff;
      padding: 20px 0; }
      .Index-wrapper .chart2 .team-table .imgSmall {
        height: 60px;
        width: 90px; }
      .Index-wrapper .chart2 .team-table .qrcode {
        width: 45px;
        height: 55px; }

